    this.size$ = new BehaviorSubject(null); //added
    this.items$ = this.size$.switchMap(size =>  //added
      db.list('/User', ref =>  //added
        size ? ref.orderByChild('size').equalTo(size) : ref  //added
      ).snapshotChanges() //added
    );

